@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500;600;700&family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen  and (min-width:900px)
{
.modalDialogueBig{
 width:500px;
}
}

.modal-dialog{
    padding: 17px!important;
}

.members-container{
    display: grid;
}

.input-group{
    flex-wrap: nowrap !important;

}

.input-group .form-control{
    width: 100%;
}

.input-group .member-group {
    width: 60%;
    margin-top: 5px;
}

.input-group .role-group {
    width: 60%;
    margin-top: 5px;
    position: relative;
    right: -10px;
}
.labels-input {
    position: relative;
    left: 2px;
    display: flex;
    grid-gap:85px;
    gap:85px;
}

.clear-button{
    font-size:1rem !important;
    position: relative;
    right: -10px;
}
.add-member-button {
        font-size: 1.1rem !important;
            background-color: #212529;
            color: white;
}
.delete-button{
    align-self: center;
}

.title-bar{
        background-color: rgba(237, 242, 248, 0.418);
        padding: 15px;
        font-weight: 700;
        color: black;
}

.my-menu{
    margin: -13px !important;
}
.card-temp {
    box-shadow: -10px 10px 60px -6px rgba(86, 88, 89, 0.44);
    background: #E0EAFC;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #CFDEF3, #E0EAFC);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    position: relative;
}
.card-temp:hover {
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    background-color: #1976d27d;
}

.card-body-temp {
    background-color:#e2e5e948;
    border-radius: 15px;
    font-size: 15px; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  
    min-height: 200px,
}


.title{
    font-size: 16px !important;
    font-weight: 700 !important;
    display: flex;
    justify-content: space-between;
    
}

.card-main-container{
    height: 180px;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0px !important;
}

.icons-container-project{
    grid-gap:8px!important;
    gap:8px!important;
    display: flex;
    position:absolute;
    flex-direction: column;
    right: 10px;
    bottom:5px;
   
}


.links {
    text-decoration: none;
    color: rgb(50, 47, 47);
    font-weight: bold;
    font-size: 14px;
    margin: 0;
}

.dates-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-basis: auto;
    grid-gap:3px;
    gap:3px
}

.date{
    font-weight: normal;
    font-size: 1em;
}



@media only screen and (max-width: 300px) {
    .card-temp {
           min-height: 300px;
        }


.card-body-temp {
    justify-content:space-evenly;
}
}

