.card-header-modif{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #E0EAFC;
        /* fallback for old browsers */
        background: -webkit-linear-gradient(to bottom, #CFDEF3, #E0EAFC);
        /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to bottom, #CFDEF3, #E0EAFC);
        /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}




.icons-container-tasks {
    gap: 6px !important
}

.title{
  
    margin: 0;
}

.header-icon{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;

}
.header-icon Button{
    font-size: 12px;
    font-weight: 700;
}

.dates-container-2{
    display: flex;
    align-items: center;
    gap: 5px;
}
.dates{
    font-size: 0.8rem;
    font-weight: 800;
}

.main-container {
    padding: 10px;
}

.card-header{
    background-color: white;
    border:none;
  
}


.card-footer{
    background-color: white;
    border: none;
    border-top: 1px solid aliceblue;
    position: relative;
    border-top-width: 1px;
}

.footer-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:5px;
    padding: 12px;
}

.project-bar {
    background-color: rgba(237, 242, 248, 0.418);
    padding: 15px;
    font-weight: 700;
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.icons{
    display: flex;
    align-items: center;
    gap:15px
}

.status-button{
    border: none;
    padding:8px;
    border-radius: 5px;
    background-color: rgb(216, 167, 52);
    color: white;
}

.more-button {
    border: none;
    border-radius: 5px;
    background: #E0EAFC;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #CFDEF3, #E0EAFC);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #CFDEF3, #E0EAFC);
    color:black;
    
}




@media only screen and (min-width: 1500px) {
    .task-card {
        width: 450px;
        margin: auto;
    }
}