@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500;600;700&family=Space+Mono:wght@400;700&display=swap');

.card-temp {
    -webkit-box-shadow: -30px 10px 60px -6px rgba(86, 88, 89, 0.44);
    -moz-box-shadow: -30px 10px 60px -6px rgba(86, 88, 89, 0.44);
    box-shadow: -10px 10px 60px -6px rgba(86, 88, 89, 0.44);
    background: #E0EAFC;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #CFDEF3, #E0EAFC);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #CFDEF3, #E0EAFC);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    position: relative;
}
.card-temp:hover {
    transform: scale(1.03);
    background-color: #1976d27d;
}

.card-body-temp {
    background-color:#e2e5e948;
    border-radius: 15px;
    font-size: 15px; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  
    min-height: 200px,
}


.title{
    font-size: 16px !important;
    font-weight: 700 !important;
    display: flex;
    justify-content: space-between;
    
}

.card-main-container{
    height: 180px;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0px !important;
}

.icons-container-project{
    gap:8px!important;
    display: flex;
    position:absolute;
    flex-direction: column;
    right: 10px;
    bottom:5px;
   
}


.links {
    text-decoration: none;
    color: rgb(50, 47, 47);
    font-weight: bold;
    font-size: 14px;
    margin: 0;
}

.dates-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-basis: auto;
    gap:3px
}

.date{
    font-weight: normal;
    font-size: 1em;
}



@media only screen and (max-width: 300px) {
    .card-temp {
           min-height: 300px;
        }


.card-body-temp {
    justify-content:space-evenly;
}
}
